.footer-text {
  position: absolute;
  bottom: 10vh;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-family: 'Kode Mono', monospace;
  font-size: 3em;
  text-align: center;
  font-style: normal;
  white-space: nowrap;
  letter-spacing: 0.2em;
  font-optical-sizing: auto;
}

.sun-image {
  position: absolute;
  left: -38%; /* Adjust as needed to position it way off to the left */
  top: 50%; /* Center vertically if needed */
  transform: translateY(-50%); /* Center vertically */
  width: 85vh; /* Adjust width as needed */
  height: 85vh; /* Adjust height as needed */
  border-radius: 50%; /* Makes the edges rounded */
  object-fit: cover; /* Ensures the image covers the container */
  z-index: 1; /* Ensures the image is above other content */
}